<template>
  <div  ref="wrapper">
    <div >
        <slot></slot>
    </div>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
/* import Pullup from 'better-scroll/pull-up'

BScroll.use(Pullup) */

export default {
    data(){
      return{
          bs:'null',
          aa:123
      }
  },
    mounted(){
   this.bs = new BScroll (this.$refs.wrapper,{
       scrollY: true,
       scrollX: true,
       startX:0,
       click:true,
       pullUpLoad:true,
       probeType: 3
   })
   
   this.bs.on('pullingUp', ()=>{
     this.$emit('pullingUp')
   })

  this.bs.on('scroll', (position) => {
    this.$emit('scrollPosition',position)
  })
  },
  methods:{
    finishPullUp(){
      this.bs.finishPullUp();
    },

  }
}
</script>

